	module.exports = {"data":{"wordpressPost":{"title":"Easy Ways To Break Out Of A Beauty Rut","content":"<p>It&#8217;s only natural &#8211; you find a beauty look that works and so you stick with it.</p>\n<p>But every now and then it&#8217;s fun to bust out and try something totally new. <strong>Sure, it&#8217;s an excellent excuse to buy more makeup</strong>. But more importantly it will make you look (and feel) brand new and there&#8217;s something about that we just love.</p>\n<p>Here, the easiest (and most on trend) ways to revamp your beauty look today.</p>\n<p><strong>A New Lip Colour</strong></p>\n<p>A new and totally different lip colour is the quickest way to reinvent. If you&#8217;re generally a natural lip shade woman, try something bold. Right now we are all about orange. If you&#8217;re pale skinned, try a more red-orange tone. If you&#8217;re darker skinned, look for true oranges.</p>\n<p>If you simply can&#8217;t come at orange, try hot pink fuchsia.</p>\n<p>If you&#8217;re already rocking the bold shades, consider a rosy pink or even a nude.</p>\n<img class=\"alignnone size-medium wp-image-1818\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/04/22212322/Winky-Lux-JPG-135x300.jpg\" alt=\"\" width=\"135\" height=\"300\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/04/22212322/Winky-Lux-JPG-135x300.jpg 135w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/04/22212322/Winky-Lux-JPG.jpg 412w\" sizes=\"(max-width: 135px) 100vw, 135px\" /><img class=\"alignnone size-medium wp-image-1820\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/04/22212630/NARS-JPG-149x300.jpg\" alt=\"\" width=\"149\" height=\"300\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/04/22212630/NARS-JPG-149x300.jpg 149w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/04/22212630/NARS-JPG.jpg 452w\" sizes=\"(max-width: 149px) 100vw, 149px\" />\n<p><strong><em>For bold new shades we love <a href=\"http://www.mecca.com.au/winky-lux/matte-lip-velour/V-025355.html\">Winky Lux Matte Lip Velour in Crush, $23</a>, and <a href=\"http://www.mecca.com.au/nars/audacious-lipstick/V-019305.html\">NARS Audacious Lipstick in Greta, $49.</a></em></strong></p>\n<p><strong>A New Eye Palette</strong></p>\n<p>If you&#8217;ve been wearing classic shadow shades (tawny browns, caramels, creams and so on) it&#8217;s time to step it up a notch. A smokey eye style will make you feel brand new. To keep it modern consider super deep shadows &#8211; even black.</p>\n<p>Women who are already smoking it up, might like to try a neutral or even entirely bare eye for a sweet, fresh-faced beauty look. If you&#8217;re feeling bold, add a teeny smear of Vaseline. It looks modern plus hydrates skin. Don&#8217;t forget lashes.<img class=\"size-medium wp-image-484 alignright\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2016/09/13131711/clump-crusher-by-lashblast-mascara-62496-300x300.jpg\" alt=\"\" width=\"300\" height=\"300\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2016/09/13131711/clump-crusher-by-lashblast-mascara-62496-300x300.jpg 300w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2016/09/13131711/clump-crusher-by-lashblast-mascara-62496-150x150.jpg 150w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2016/09/13131711/clump-crusher-by-lashblast-mascara-62496-768x768.jpg 768w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2016/09/13131711/clump-crusher-by-lashblast-mascara-62496-1024x1024.jpg 1024w\" sizes=\"(max-width: 300px) 100vw, 300px\" /></p>\n<img class=\"alignnone size-medium wp-image-1822\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/04/22213614/MAC_DarkDesires_IntoTheWellEyeShadow_BegForIt_300dpiCMYK-232x300.jpg\" alt=\"\" width=\"232\" height=\"300\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/04/22213614/MAC_DarkDesires_IntoTheWellEyeShadow_BegForIt_300dpiCMYK-232x300.jpg 232w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/04/22213614/MAC_DarkDesires_IntoTheWellEyeShadow_BegForIt_300dpiCMYK-768x994.jpg 768w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/04/22213614/MAC_DarkDesires_IntoTheWellEyeShadow_BegForIt_300dpiCMYK-791x1024.jpg 791w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/04/22213614/MAC_DarkDesires_IntoTheWellEyeShadow_BegForIt_300dpiCMYK.jpg 1545w\" sizes=\"(max-width: 232px) 100vw, 232px\" />\n<p><strong><em>Try <a href=\"http://www.maccosmetics.com.au/products/13840/Products/Makeup/Eyes/Shadow\">M.A.C Desires Into The Well Eye Shadow, $35</a>, in any of the collection&#8217;s six shades. For lashes we love <a href=\"https://www.priceline.com.au/covergirl-clump-crusher-by-lashblast-13-1-ml\">Covergirl Clump Crusher by Lash Blast, $18.95.</a></em></strong></p>\n<p><strong>A Bold New Brow</strong></p>\n<p>Finally, changing the shape and/or shade of your brows will significantly revamp your beauty look. The great thing about that is you can do it with a brow pencil or shadow and in less than 60 seconds.</p>\n<p>If you&#8217;re after inspiration, hop onto Instagram and type in #brows. There are literally thousands of killer styles to try. And if you decide you&#8217;d like to keep your new shape, visit with a brow pro who can help you make it more permanent.</p>\n<p><em><strong><a href=\"http://shop.davidjones.com.au/djs/en/davidjones/brow-kit-2858-90057--1\">Try Bobbi Brown Brow Kit, $75</a>.</strong></em></p>\n<img class=\"size-medium wp-image-1831 alignleft\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/04/22222633/BB-JPG-300x218.jpg\" alt=\"\" width=\"300\" height=\"218\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/04/22222633/BB-JPG-300x218.jpg 300w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/04/22222633/BB-JPG.jpg 470w\" sizes=\"(max-width: 300px) 100vw, 300px\" />\n","better_featured_image":{"source_url":"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/02/27130739/beauty_leiane_tuesday3959.jpg"},"categories":[{"slug":"makeup","name":"Makeup"}]},"site":{"siteMetadata":{"title":"The Beauty Insider"}}},"pathContext":{"id":"da231084-1ef2-5069-9a39-97b8ba269b20"}}

